@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400&display=swap);
* {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

.Rooms{
    color: rgb(175, 175, 175);
    font-family: 'Fira Code', sans-serif;
    background-color: rgb(22, 22, 22);
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.formBox{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2em;
    height: 22em;
    border: 2px rgb(177, 177, 177) solid;
}

.Rooms h1{
    font-size: 31px;
    margin: 0;
    margin-bottom: 1.5em;
}

.Rooms input{
    margin-top: 10%;
    margin-bottom: 10%;
    display: block;
    background-color: transparent;
    outline: none;
    color: rgb(209, 209, 209);
    border: none;
    font-size: large;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
}

.goButton{
    background-color: rgb(241, 241, 241);
    color: rgb(53, 53, 53);
    font-family: 'Fira Code', sans-serif;
    font-size:1.3em;
    width: 50%;
    margin-top: 0.7em;
    border: none;
    -webkit-transition: background-color .5s, color .5s;
    transition: background-color .5s, color .5s;
    border-radius: 0.1em;
}

.goButton:hover{
    color: rgb(241, 241, 241); 
    background-color: rgb(41, 41, 41);
}

.Rooms input::-moz-selection{
    color: lightgreen;
}

.Rooms input::selection{
    color: lightgreen;
}

.Table{
    height: 45vw;
    width: 80vw;
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-row-gap: .3vw;
    row-gap: .3vw;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 1vw;
    margin-left: 1vw;
    align-items: center;
    position: relative;
}

.winner{
    position: fixed;
    z-index: 100;
    height: 100vh;
    width: 82vw;
    left: 0px;
    top: 0px;
    background-color: rgba(0, 0, 0, 0.925);
    font-size: 5rem;
    text-align: center;
    color: rgb(255, 255, 255);
}

.center{
    margin: 0;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 100%;
    text-align: center;
}

.winnerSubtext{
    color: rgb(114, 114, 114);
    margin-bottom: 0vh;
    font-size: 0.3em;
    display: none;
}

.Table button{
    position: fixed;
    top: 10px;
    left: 10px;
}

.cooldown{
    position: absolute;
    top:0;
    height: 1.5em;
    width: 80%;
    display: block;
    border:2px solid black;
    left: 50%;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
    z-index: 40;
}
.cooldown  > .proc{
    background-color: rgb(218, 57, 57);
    width: 0%;
    height: 100%;
}.cooldown > .procAnimation{
    opacity: 0;
    -webkit-animation: cool 10s linear 0s normal !important;
            animation: cool 10s linear 0s normal !important;
}

@-webkit-keyframes cool{
    0%{
        width: 0%;
        opacity: 1;
    }
    99%{
        width: 99%;
        opacity: 1;
    }
    100%{
        width: 100%;
        opacity: 0;
    }
}

@keyframes cool{
    0%{
        width: 0%;
        opacity: 1;
    }
    99%{
        width: 99%;
        opacity: 1;
    }
    100%{
        width: 100%;
        opacity: 0;
    }
}

@media only screen and (max-width: 740px) {
    .Table{
        grid-auto-flow: row;
        width: 100vw;
        height: 80vh;
        margin-bottom: 2vh;
        margin-top: 2vh;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    }
    .winner{
        height: 84vh;
        width: 100vw;
    }
    .cooldown{
        top: auto;
        top: initial;
        bottom: -3em;
        height: 1em;
        width: 100vw;
    }
}

.card{
    background-color: rgb(245, 245, 245);
    border-style: solid;
    border-width: 0.07rem;
    border-radius: 3%;
    height: 100%;
    width: 80%;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 1;
    z-index: 41;
}

.card img{
    height: auto;
    width: 50%;
}

.card .hidden{
    background-color: rgb(255, 255, 255);
    height: 100%;
    width: 100%;
}
.card .imgBox{
    background-color: rgb(28, 31, 43);
    height: calc(100% - 10px);
    width: calc(100% - 10px);
    border-radius: 3%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card:hover{
    border-color: rgb(167, 0, 0);
    background-color: rgb(214, 214, 214);
}

.selectedCard{
    background-color: rgb(228, 227, 155) !important;
    border-color: rgb(255, 0, 0) !important;
}

.badCard{
    border-color: red !important;
    border-width: 5px !important;
}

.movements{
    -webkit-animation: eni 1s ease-out 0s normal !important;
            animation: eni 1s ease-out 0s normal !important;
}

@-webkit-keyframes eni{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@keyframes eni{
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@media only screen and (max-width: 740px) {
    .card{
        height: 23vw;
        width: 32vw;
        flex-direction: row;
    }
}
svg{
    margin: auto;
    width: 50%;
}


.svgContainer{
    display: flex;
    justify-content: center;
}

@media only screen and (max-width: 740px){
    svg{
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        width: 2em;
    }
}
.Scoreboard{
    height:100vh;
    background-color: #1a1a1a;
    width: 18vw;
    color: #eeeeee;
    font-size: 1.5em;
    position: relative;
    right: 0;
}

.hs{
    text-align: center;
    width: 100%;
}

.u{
    color: rgb(184, 63, 253);
}

.offline{
    color: rgba(255, 255, 255, 0.192);
}

.playersList{
    height: 50%;
    overflow-y: hidden;
    overflow-x: hidden;
    padding-left: .3em;
}

.playersList div{
    height: 1em;
    overflow: hidden;
    width: 100em;
}

.Scoreboard > button{
    display: block;
    font-size: 1em;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    bottom: 1rem;
    width: 90%;
    height: 4rem;
    -webkit-transition: background-color .2s ease-out;
    transition: background-color .2s ease-out;
    border: none;
    border-radius: .2rem;
    outline: none;
    color: rgb(214, 202, 183);
    font-family: 'Fira Code', sans-serif;
}

.Scoreboard .unclicked{
    background-color: rgb(88, 42, 114);
}
.Scoreboard .clicked{
    background-color: rgb(53, 39, 61);
}


@media only screen and (max-width: 740px) {
    .Scoreboard{
        width: 100vw;
        position: relative;
        padding-top: 1em;
    }
    .Scoreboard button{
        display: block;
        position:  absolute;
        width: 4em;
        font-size: .7em;
        top: 1.5em;
        bottom: auto;
        bottom: initial;
        right: .2em;
        left: auto;
        left: initial;
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
        height: calc(16vh - 2em);
        margin-top: .2em;
        margin-bottom: .2em;
    }
    .hs{
        display: block;
        text-align: left;
        width: 60%;
        margin-top: 0.1em;
        margin-bottom: 0.1em;
        padding-left: 0.3em;
        font-size: .8em;
    }
    .playersList{
        font-size: .7em;
        width: 65%;
    }
    .playersList > *{
        height: 1.2em !important;
    }
}


.Chat{
    width: 100%;
    height: 8em;
    font-family: 'Roboto', sans-serif;
    padding-top: .2em;
    background-color: azure;

}
.Chat .messagesScreen{
    width: 100%;
    height: 85%;
    color: black;
    overflow-y: scroll;
    overflow-x: hidden;
    font-size: .7em;
} 
.Chat .messagesScreen > div{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.Chat .message.nth{
    border-top-left-radius: 2px;
}
.Chat .message.me.nth{
    border-top-left-radius: 7px;
}
.Chat .message.first{
    margin-top:1em;
}
.Chat .message{
    position: relative;
    display: inline-block;
    max-width: 90%;
    margin-left: .5em;
    padding-right: .6em;
    padding-left: .6em;
    border-radius: 7px;
    border-bottom-left-radius: 2px;
    border: 1px solid rgba(0, 0, 0, 0.253);
    background-color: rgb(217, 240, 243);
}
.Chat .message.me{
    background-color: burlywood;
    align-self: flex-end;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 2px;
}
.Chat .text{
    width: auto;
    display: inline-block;
    word-wrap: break-word;
    max-width: 100%;
    font-size: .9em;

}
.Chat .author{
    position: absolute;
    font-size: .6em;
    top: -1.2em;
    left: .5em;
    color: rgba(0, 0, 0, 0.712);
}

.Chat .options{
    display: flex;
    flex-direction: row;
    height: 15%;
}
.Chat button{
    height: 100%;
    display: block;
    font-family: 'Roboto', sans-serif;
}
.Chat input{
    height: 100%;
    flex: 1 1;
    outline: none;
    font-family: 'Roboto', sans-serif;
}
.Game{
    margin:0px;
    display: flex;
    flex-wrap: wrap;
    height: 100vh;
    padding: 0px;
    background-color: rgb(114, 90, 128);
    font-family: 'Fira Code', sans-serif;
}

*::-moz-selection{
    color: tomato;
}

*::selection{
    color: tomato;
}

*{
    box-sizing: border-box;
}
